	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter - Forty by HTML5 UP","author":"Hunter Chang"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/fast-and-fresh-loading-graphics-with-ajaxloadinfo/index.md absPath of file >>> MarkdownRemark","html":"<p align=\"left\">Here&apos;s a neat little site - <a title=\"Open link in a new window\" href=\"http://www.ajaxload.info/\" target=\"_blank\">ajaxload</a>.&#xA0; There&apos;s nothing better than letting your user know when they&apos;re waiting for something, and although a simple animation can&apos;t tell them the exact progress, or how much longer they&apos;ll have to wait - it&apos;s often the thought that counts.&#xA0; Your preloader / loading animation is that little extra bit of finess&#xE9; for your creations.</p>\n<p align=\"left\">And ajaxload has a lovely little generator to knock some graphics up for you, in a variety of formats and completely customizable colours.</p>\n<p align=\"center\"><img src=\"/content/images/2007/10/ajax-loader3.gif\" alt=\"\"> <img src=\"/content/images/2007/10/ajax-loader4.gif\" alt=\"\"> <img src=\"/content/images/2007/10/ajax-loader.gif\" alt=\"\"></p>","frontmatter":{"title":"Fast and fresh loading graphics with ajaxload.info","date":"October 21, 2007","tags":["design","external-links"]}}},"pathContext":{}}

